//     html  

/**
 *   timer  index.html
 */

export function timerComp() {
    document.querySelector('main').insertAdjacentHTML('beforeend', `    
    <div class="timer">    
        <strong>     ?</strong>
        <div class="timerBlock">
            :<input type="number" name="" id="days" min="0" value="0">
            :<input type="number" name="" id="hours" min="0" value="0">
            :<input type="number" name="" id="minutes" min="0" value="0">
            :<input type="number" name="" id="seconds" min="0" value="0">
            <button class="buttonTimerStart">Start</button>
            <button class="buttonTimerStop">Stop</button>
            <button class="buttonTimerResume">Resume</button>
        </div>

        <strong> :</strong>
        <div class="timeLeft">
            :<div class="days">0</div>
            :<div class="hours">0</div>
            :<div class="minutes">0</div>
            :<div class="seconds">0</div>
        </div>
        <div class="timerMsg"></div>
    </div>`
    )
};
